<style scoped>
#single-blog {
  max-width: 960px;
  margin: 0 auto;
  background: #eee;
  border: 1px dotted #aaa;
  padding: 20px;
}
</style>
<template>
  <div id="single-blog">
    <h1>{{blog.title}}</h1>
    <div>{{blog.content}}</div>
    <div>作者：{{blog.author}}</div>
    <ul>
      <li v-for="category in blog.categories">分类：{{category}}</li>
    </ul>
    <button type="button" v-on:click="deleteSingleBlog()">删除</button>
    <router-link v-bind:to="'/edit/'+ JSON.stringify(blog) ">
      <button type="button" @click="updateSingleBlog()">编辑</button>
    </router-link>
  </div>
  <!-- <panle-confirm></panle-confirm> -->
</template>

<script>
import Confirm from "./Confirm"; 
// import ShowBlogs from "./ShowBlogs"; 
import AV from "leancloud-storage"; //LeanCloud 请求数据源
export default {
  name: "Single-Blog",
  data() {
    return {
      id: this.$route.params.id,
      blog: {}
    };
  },
  // comments:{
  //   'panle-confirm':Confirm
  // },
  created() {
    let that = this;
    var query = new AV.Query("MyBlog");
    query.find().then(function(data) {
      let blog = [];
      that.blog = data[0].attributes;
    });
  },
  methods: {
    //删除
    deleteSingleBlog() {
      let that = this;
      let BeleteBlog = AV.Object.createWithoutData("MyBlog", that.id);
      BeleteBlog.destroy().then(function(data) {
        console.log("删除结果：", data);
        window.history.back();
      });
    },
    updateSingleBlog() {}
  }
};
</script>




