<style scoped>
#editblog * {
  box-sizing: border-box;
}

#edit-blog {
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}
label {
  display: block;
  margin: 20px auto;
}

input[type="text"],
textarea,
select {
  display: block;
  width: 100%;
  padding: 8px;
}

textarea {
  height: 200px;
}

#checkboxes label {
  display: inline-block;
  margin-top: 0;
}

#checkboxes input {
  display: inline-block;
  margin-right: 10px;
}

button {
  display: block;
  margin: 20px 0px;
  padding: 10px 30px;
  background: crimson;
  color: #ffffff;
  border: 0;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}

#preview {
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}

h3 {
  margin-top: 10px;
}
</style>
<template>
  <div id="edit-blog">
    <h2>编辑博客</h2>
    <form>
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required>
      <label>博客内容</label>
      <textarea type="blogtext" v-model="blog.content"></textarea>
      <div id="checkboxes">
        <label>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories">
        <label>node.js</label>
        <input type="checkbox" value="node.js" v-model="blog.categories">
        <label>React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories">
      </div>
      <div>
        <label>作者：</label>
        <select v-model="blog.author">
          <option v-for="author in authors">{{author}}</option>
        </select>
      </div>
      <button v-on:click.prevent="btnpost">提交</button>
    </form>
    <!-- <div v-if="!blog.submmited">
      <h2>恭喜，您的博客已经修改成功！</h2>
    </div>-->

    <hr>
    <div>
      <h3>博客总览</h3>
      <div id="preview">
        <p>博客标题:{{blog.title}}</p>
        <p>博客内容</p>
        <p>{{blog.content}}</p>
        <p>博客分类</p>
        <ul>
          <li v-for="catefory in blog.categories">{{catefory}}</li>
        </ul>
        <p>作者：{{blog.author}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import AV from "leancloud-storage"; //LeanCloud 请求数据源
export default {
  name: "edit-blog",
  data() {
    return {
      blog: {
        title: "",
        content: "",
        categories: [],
        author: "",
        submmited: true
      },
      authors: ["haozi", "莫言", "大同"],
      pramblog: this.$route.params.blog
    };
  },

  created() {
    if (this.pramblog != undefined) {
      let pramblog = JSON.parse(this.pramblog);
      this.blog = pramblog;
      console.log('先输出结果：',JSON.parse( pramblog.category))
      this.blog.categories = JSON.parse( pramblog.category);
    }else{

    }









    // this.$http
    //   .post("https://myblogs-cd753.firebaseio.com/posts.json", this.blog)
    //   .then(data => {
    //     console.log("data:", data);
    //     if (data.ok) {
    //       alert("已创建！");
    //       this.blog.submmited = false;
    //     }
    //   });
  },
  methods: {
    btnpost(){
                let MyBlog = AV.Object.extend("MyBlog");
      let myblog = new MyBlog();
      console.log('先输出下：',this.blog.categories);
      myblog.set("title", this.blog.title);
      myblog.set("content", this.blog.content);
      myblog.set("author", this.blog.author);
      myblog.set("category", JSON.stringify(this.blog.categories));
      myblog.save().then(function(data) {
        console.log("保存结果：", data);
      });
      }
  },
};
</script>




