// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueResource from 'vue-resource'; //网络请求 类似于ajax
import VueRouter from 'vue-router'; //管理路由
import AV from 'leancloud-storage' //LeanCloud 请求数据源
import App from './App'
import Routes from './routers' //路由管理界面


Vue.config.productionTip = false
Vue.use(VueResource)
Vue.use(VueRouter)


AV.init({
  appId: '5nlvHhhA2FfzKecydoyPSTYW-gzGzoHsz',
  appKey: 'e4KOnkGui0GeROtgTkQUetzc',
})





 











//全局自定义指令
// Vue.directive('rainbow', {
//   bind(el, bindling, vnode) {
//     el.style.color = "#" + Math.random(16).toString().slice(2, 8);
//   }
// })

Vue.directive('theme', {
  bind(el, bindling, vnode) {
    if (bindling.value == 'wide') {
      el.style.maxWidth = "1260px";
    } else if (bindling.value == 'narrow') {
      el.style.maxWidth = "600px;"
    }

    if (bindling.arg == 'back') {
      el.style.background = "#6677cc";
      el.style.padding = "20px";
    }
  }
})

//全局自定义过滤器
// Vue.filter('to-uppercase', function (value) {
//   return value.toUpperCase(); //让字母变成大写
// })

Vue.filter('snippet', value => {
  return value.slice(0, 120) + '...';
})




//创建路由
const router = new VueRouter({
  routes: Routes,
  mode: "history"
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: {
    App
  },
  template: '<App/>',
  router,
})



