<style scoped>
nav{
  background-color: crimson;
  padding: 30px 0;
  margin-bottom: 40px;
}

ul{
  list-style-type: none;
  text-align: center;
  margin: 0;
}
li{
  display: inline-block;
  margin: 0 10px;
}
a{
  text-decoration: none;
  padding: 12px;
  color: #ffffff;
  border-radius: 5px;
}
.router-link-active{
  background: rgba(255, 255, 255, 0.8);
  color: #444;
}
</style>
<template>
  <nav>
    <ul>
      <li >
        <router-link to="/" exact>博客</router-link>
      </li>
      <li>
        <router-link to="/add" exact>写博客</router-link>
      </li>
    </ul>
  </nav>
</template>

<script>
export default {
  name: "blog-Header",
  data(){
    return {};
  },
  created() {
    
  },
};
</script>



