<style>
#showblogs {
  max-width: 800px;
  margin: 0 auto;
}

.single-blog {
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background: #eee;
  border: 1px dotted #aaa;
}

.single-blog a {
  color: #444;
}

input[type="text"] {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
}
</style>

<template>
  <div v-theme:back="'narrow'" id="showblogs">
    <h1>博客总览</h1>
    <input type="text" v-model="search" placeholder="请输入...">
    <div v-for="(blog,index) in filteredBlogs" class="single-blog">
      <router-link v-bind:to="'/blog/'+blog.id">
        <h2 v-rainbow>{{blog.title | to-uppercase }}</h2>
      </router-link>
      <article>{{blog.content | snippet}}</article>
    </div>
  </div>
</template>

<script>
import AV from "leancloud-storage"; //LeanCloud 请求数据源
export default {
  name: "show-blogs",
  data() {
    return {
      bolgs: [],
      search: ""
    };
  },
  computed: {
    filteredBlogs() {
      return this.bolgs.filter(blog => {
        return blog.title.match(this.search); //返回boolbean类型
      });
    }
  },

  //局部自定义过滤器
  filters: {
    "to-uppercase": function(value) {
      return value.toUpperCase();
    }

    // toUpperCase(value) {
    // return  value.toUpperCase(); //toUpperCase();  让字母变成大写。
    // }
  },

  //局部自定义指令
  directives: {
    rainbow: {
      bind(el, binding, vnode) {
        //console.log('随机数：',Math.random());
        //console.log('随机数截图：',Math.random().toString().slice(2,8));
        el.style.color =
          "#" +
          Math.random(16)
            .toString()
            .slice(2, 8);
      }
    }
  },
  created() {
    let that=this;
    var query = new AV.Query("MyBlog");
    query.find().then(function(data) {
      let blogArrays = [];
      console.log('ss',data);
      for (let item in data) {
           data[item].attributes.id=data[item].id
        blogArrays.push(data[item].attributes);
      }
      console.log('数组：',blogArrays)
      that.bolgs = blogArrays;
    });
  }
};
</script>




